import React from "react";


const Shop = () => {
  return (
    <>
      <div className="shop">
        <div className="container">
          <div className="shop_text">
            <div>
              <h1>Free Delivery With Pizza Of The Day only 8.99 USD </h1>
              <div className="shop_btn">
                <a href="!#" className="btn btn_smart">
                  Shop Now
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Shop;
